$tim: #87509c; //Tím
$ngoc: #17c2a4; // Cyan
$vang: #ffdd99; // Vang
$do: #d74680; // Đỏ
$xanh: #3c5499; // Xanh
$cam: #EB7D4B; // Cam

// Đây là phần Header
.Home_Header {
    background: $tim;
    align-items: center;
    justify-content: center;
    text-align: center;
    .nav-wrap {
        list-style: none; }
    .text a {
        color: #ffffff;
        padding: 20px;
        font-size: 1.2rem; }
    .text a:hover {
        background: #4C5965;
        text-decoration: none;
        border-radius: 20%; }

    .nav-wrap li {
        display: inline-block; } }




// Đây là phần Sliders
.Home_Sliders {
    background: $tim;
    min-height: 500px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h1 {
        color: #ffffff;
        font-size: 2.5rem;
        letter-spacing: 1px; }
    .btn-more {
        text-transform: uppercase;
        font-size: 1.2rem;
        background: $cam;
        color: #ffffff;
        padding: 20px 40px;
        margin: 50px 0px;
        border: 1px solid $cam;
        box-shadow: 0px 5px 0px #C86A40; }
    a.btn-more:hover {
        background: #C86A40;
        text-transform: uppercase;
        font-weight: bolder; } }
// Đây là phần Services
.Home_Services {
    background: $ngoc;
    min-height: 650px;
    // display: flex
    justify-content: center;
    align-items: center;
    text-align: center;
    h2 {
        color: #ffffff;
        text-transform: uppercase;
        position: relative;
        font-size: 3rem;
        padding-bottom: 30px;
        padding-top: 20px;
        letter-spacing: 2px;
        &:before,
        &:after {
            content: '';
            display: block;
            background: #485351;
            position: absolute;
            left: 50%;
            transform: translateX(-50%); }
        &:before {
            width: 80px;
            height: 5px;
            bottom: 8px; }
        &:after {
            width: 20px;
            height: 10px;
            bottom: 6px; } }
    p.slogun {
        color: #ffffff;
        font-size: 1.3rem;
        padding-bottom: 20px; }
    .figure img {
        padding-bottom: 50px;
        padding-top: 20px; }
    h3 {
        color: #ffffff;
        padding-bottom: 20px; }
    p.branding {
        color: #ffffff; } }
